export default {
	data() {
		return {
			modalName: null,
			longitude: '',
			latitude: '',
			mapInfo: '',
			district: '',
			markers: [],
			hospitalInfo: '',
			hospital_apicode: '6abdc134cce74b2cb166de18d80620fa',

			// 动画
			animation: '',
			toggleDelay: false

		}
	},
	onLoad() {
		console.log('onLoad---');
	},
	onShow: function() {
		console.log('show')
		var that = this
		that.mapInfo = global.loadMap()
		this.loadMap()
		this.mapMarkers()
	},
	onShareAppMessage: function() {
		return {
			title: "附近医院",
			path: '/pages/hospital/hospital'
		}

	},

	methods: {
		// 联系医院
		callPhone(e){
			uni.makePhoneCall({
				phoneNumber:e.currentTarget.dataset.phone
			})
		},
		// 拉开医院抽屉
		showHospital(e) {
			// 按钮动画
			this.Toggle(e)
			// 加载医院
			this.loadHospital()
			// 打开抽屉
			this.showModal(e)
			// 延迟显示
			this.ToggleDelay()
		},
		// 实时搜索
		search_Change(e) {
			var that = this
			let list = that.hospitalInfo.result
			let count = 0
			for (let i = 0; i < list.length; i++) {
				let a = e.detail
				let b = list[i].name
				if (b.search(a) != -1) {
					console.log(b.search(a))
					list[i].isShow = true
					count++
				} else {
					list[i].isShow = false
				}
			}
			that.hospitalInfo.result = list
			that.hospitalInfo.size = count
			this.ToggleDelay()
		},


		Toggle(e) {
			var that = this
			var anmiaton = e.currentTarget.dataset.class;
			that.animation = anmiaton;
			setTimeout(() => {
				that.animation = '';
			}, 1000)
		},
		ToggleDelay() {
			this.toggleDelay = true;
			setTimeout(() => {
				this.toggleDelay = false
			}, 1000)
		},

		// 打开隐藏抽屉
		showModal(e) {
			this.modalName = e.currentTarget.dataset.modal
		},
		hideModal(e) {
			this.modalName = null
		},
		// 渲染地图数据
		loadMap() {
			var that = this
			that.latitude = that.mapInfo.latitude
			that.longitude = that.mapInfo.longitude
			that.district = that.mapInfo.regeocodeData.addressComponent.district
		},
		// 调用接口加载附近医院
		loadHospital() {
			var that = this
			console.log(that.mapInfo)
			// var city = that.mapInfo.regeocodeData.addressComponent.city
			uni.request({
				url: "http://api.yonyoucloud.com/apis/dst/hospitalInfoQuery/hospitalInfoQuery",
				data: {
					"name": that.district
				},
				method: "GET",
				header: {
					"authoration": "apicode",
					"apicode": that.hospital_apicode
				},
				success(res) {
					console.log("医院如下")
					console.log(res.data)
					let hospitalInfo_isShow = res.data
					for (let i = 0; i < hospitalInfo_isShow.result.length; i++) {
						hospitalInfo_isShow.result[i].isShow = true
					}
					that.hospitalInfo = hospitalInfo_isShow

				}
			})
		},


		mapMarkers() {
			console.log('markers')
			this.markers = [{
				id: 1001,
				longitude: '114.456107',
				latitude: '22.789431',
				title: '三和医院',
				iconPath: '../../static/tabbar/hospital-tap.png',
				callout: {
					content: '三和医院 686m',
					color: '#9bf',
					fontSize: '30rpx',
					bgColor: 'red',
					padding: '30rpx',

				},
				label: {
					content: '中国石化加油站 686m',
					color: '#fff',
					padding: '10',
					textAlign: 'center',
				}
			}]
		},
		//    点击标记时触发
		markertap() {
			console.log('markertap点击标记时触发')
		},
		//mapUpdated
		mapUpdated() {
			console.log('mapUpdated----------在地图渲染更新完成时触发')
		},
		//
		closeMapMarker() {
			console.log('closeMapMarker----------点击地图时触发; App-nuve、微信小程序2.9支持返回经纬度')
		},

		//
	}
}
